*{
  font-family: Arial, Helvetica, sans-serif;
  
}
body{
  padding: 20px;
}
input{
  width: 100%;
  font-size: 1.2;
  color: grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content:center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}